@import "src/assets/styles/vars";

.mercury-cash {
  .mercury-cash__pain-info {
    padding: 0;
    text-align: center;
    list-style: none;
    margin: 0 0 0.75em;

    li {
      font-size: 0.75em;
      line-height: 1.31em;
      color: $titleLighterColor;
      margin-bottom: 5px;
      letter-spacing: 0.75px;

      strong {
        font-weight: 700;
        margin-right: 5px;
      }

      span {
        font-weight: 400;
      }
    }
  }

  .mercury-cash__pain-info--largest {
    li {
      font-size: 0.875em;

      strong {
        display: inline-block;
        margin-bottom: 5px;
      }
    }
  }
}